  .explore {  /* button  */
	position: absolute;
	font-family: 'Space Mono', monospace;
	letter-spacing: 1px;
	background: rgb(161, 175, 175);  
	color: rgb(5, 5, 5); 
	height: 30px;
	width: 120px;
	font-size: 14px;
    font-weight: bold;
	/* transition: .01s .23s ease-out all;    */
	overflow: hidden;
    left:13rem;
	top: 4rem;
}

.explore:before { 
	content: '';  
	position: absolute;
	left: 0;
	top: 0; 
	height: 100%;
	width: 0%;
	background: #918a8a;  
	z-index: 10;
	transition: .5s ease-in all;   
}

  .explore:after { 
	content: '';  
	position: absolute;
	left: -5%;
	top: 5%;
	height: 90%;
	width: 5%;
	background: rgb(153, 140, 140);
	transition: .4s .02s ease-in all;  
} 

.explore:hover {
	cursor:pointer ;
	color: transparent;
}

.explore:hover:before {
	left: 100%;
	width: 25%;
}

  .explore:hover:after {  
	left: 100%;
	width: 1rem;
	
}

.explore:hover .icon-right.after:after {  
    
	display: fixed;
	left: -7.5rem;
	top:  0;
	color: rgb(239, 228, 228);
	transition: .2s .2s ease all;
}

.explore:hover .icon-right.after:before {  
	left: -64px;
	top: 5px;
	opacity: 100%; 
	color: rgb(0, 0, 0);
}

.icon-right {
	position: absolute;
	top: 0;
	right: 0;
}

 .icon-right:after {   
	font-family: "FontAwesome";
	content: '\2192'; 
	font-size: 24px;
	display: fixed;
	position: relative;
	top: 0px;
	transform: translate3D(0, -50%, 0);
}

.icon-right.after:after {  
	left: -250px;
	color: rgb(232, 229, 229);
	transition: .15s .25s ease left, .5s .05s ease color;
}

 .icon-right.after:before {  
	content: 'Explore';
	 position: absolute; 
	left: -230px;
	top: 10px;
	opacity: 0;
	transition: .2s ease-in all; 
    font-weight: bold;
	
} 

/* button css ends */



.wrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: none; 
    background: linear-gradient(90deg, rgb(125, 141, 224) 0%, rgb(96, 130, 173)35%, rgba(0,212,255,1)100%); 
}


.box div{
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: transparent;
    border: 6px solid rgba(18, 10, 20, 0.8);

}

.box div:nth-child(1){
    top: 12%;
    left: 42%;
    animation: animate 10s linear infinite;
}

.box div:nth-child(2){
    top: 70%;
    left: 50%;
    animation: animate 7s linear infinite;
}

.box div:nth-child(3){
    top: 17%;
    left: 6%;
    animation: animate 9s linear infinite;
}
 
.box div:nth-child(4){
    top: 20%;
    left: 60%;
    animation: animate 10s linear infinite;
}

.box div:nth-child(5){
    top: 67%;
    left: 10%;
    animation: animate 6s linear infinite;
}

.box div:nth-child(6){
    top: 80%;
    left: 70%;
    animation: animate 12s linear infinite;
}

.box div:nth-child(7){
    top: 60%;
    left: 80%;
    animation: animate 15s linear infinite;
}

.box div:nth-child(8){
    top: 32%;
    left: 25%;
    animation: animate 16s linear infinite;
}

.box div:nth-child(9){
    top: 90%;
    left: 25%;
    animation: animate 9s linear infinite;
}

.box div:nth-child(10){
    top: 20%;
    left: 80%;
    animation: animate 5s linear infinite;
}

@keyframes animate{
    0%{
        transform: scale(0) translateY(0px) rotate(0);
        opacity: 1;
    }

    100%{
        transform: scale(1.3) translateY(-90px) rotate(360deg);
        opacity: 0;
    }
}

