/* button css */
.verify_btn{
  top:35rem;
  left:10rem;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'poppins',sans-serif;
}

.verify_btn{
  
  margin-bottom: 100px;
  position: absolute;
  height: 60px;
  width: 200px;
  margin: 0 35px;
  border-radius: 30px;
  border:none;
  outline: none;
  background: #30949d;
  color: rgb(36, 19, 3);
  font-size: 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
}
.verify_btn:first-child:hover{
  background: linear-gradient(90deg,#03a9f4, #30eb0f, #f51118,#1996d0);
  background-size:400%;

}



.verify_btn:first-child:before
{
  content: '';
  position: absolute;
  background: inherit;
  top: -5px;
  right: -5px;
  bottom: -5px;
  left: -5px;
  border-radius: 50px;
  filter: blur(20px);
  opacity: 0;
  transition: opacity 0.5s;
}

.verify_btn:first-child:hover:before
{
  opacity: 1;
  z-index:-1;
}
.verify_btn:hover{
  z-index:1;
  animation: glow 8s linear infinite;
}
@keyframes glow{
  0%{
      background-position:0% ;
  }
  100%{
      background-position:400% ;
}
}

