/* textbox */
 *{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 'poppins'sans-serif;

} 
.check{

   display: flex; 
  justify-content: left;
  align-items: left;
  min-height: 100vh;
  flex-direction: column;
  gap:30px;
  
  background:rgb(251, 222, 250);
}
.inputBox{
  position: relative;
  width: 250px;
}
.inputBox input{
  width: 100%;
  padding: 10px;
  top:180px;
  left: 30px;
border:2px solid red ;
   border-radius: 10px; 
  outline: none;
  color: #1a1818;
 position: relative;
  font-size: 1em;
   transition: 0.5s;
}
.inputBox span
{
  position: relative;
  top:145px;
  left: 50px;
  
  padding: 18px;
  pointer-events: none; 
  font-size:1em ;
  color: rgba(255,255,255,0,25);
  text-transform: uppercase;
  transition: 0.5s;
}
 .inputBox input:valid~span,
.inputBox input:focus~span{
color: #16ec8c;
 top:120px;       /* css for uplift   */
  border-radius: 10px; 
 transform: translateX(10px)translateY(-7px); 
font-size: 0.65em;
padding: 0 10px;
background: #1d2b3a;
 border-left:1px solid #18685f; 
 border-right:1px solid  #150ae4;  
letter-spacing: 0.2em;
} 
/* shadeing font */
.inputBox:nth-child(2) input:valid~span,
.inputBox:nth-child(2) input:focus~span
{
background: rgb(251, 249, 248);
color: rgb(90, 3, 70);
border-radius: 3px;
}   
 .inputBox input:valid,
.inputBox input:focus
{
  border: 3px solid #3716f0;
}   




